.profile {
    padding: 40px 0 50px;
    display: grid;
    grid-template-columns: 120px 1fr 150px;
}

@media(max-width:760px) {
    .profile {
        padding: 42px 0 36px;
        grid-template-columns: 1fr;
        grid-template-rows: repeat(3, auto);
        justify-items: center;
    }
}
